// components/ContactForm.tsx
'use client';
import { useState } from 'react';

export default function ContactForm(){
  const [name,setName] = useState('');
  const [email,setEmail] = useState('');
  const [msg,setMsg] = useState('');
  const [status,setStatus] = useState<'idle'|'sending'|'ok'|'error'>('idle');

  async function submit(e: React.FormEvent){
    e.preventDefault();
    setStatus('sending');
    try {
      const res = await fetch('/api/contact', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ name, email, message: msg }),
      });
      if (res.ok) setStatus('ok');
      else setStatus('error');
    } catch (err) {
      setStatus('error');
    }
  }

  return (
    <form onSubmit={submit} className="max-w-xl">
      <input className="w-full p-3 mb-3 rounded border" placeholder="Name" value={name} onChange={e=>setName(e.target.value)} />
      <input className="w-full p-3 mb-3 rounded border" placeholder="Email" value={email} onChange={e=>setEmail(e.target.value)} />
      <textarea className="w-full p-3 mb-3 rounded border" placeholder="Message" rows={5} value={msg} onChange={e=>setMsg(e.target.value)} />
      <button className="px-4 py-2 rounded bg-gradient-to-r from-brand-blue-500 to-brand-purple-500 text-white">
        {status === 'sending' ? 'Sending...' : 'Send'}
      </button>
      {status === 'ok' && <p className="mt-2 text-green-600">Thanks — message received!</p>}
      {status === 'error' && <p className="mt-2 text-red-600">Error — try later.</p>}
    </form>
  );
}
